import React from "react";
import { Link } from "react-router-dom";
import cookies from 'react-cookies';

import { images } from "../../constants";
import "../../css/bootstrap.min.css";
import "./Services.scss";

const services = [
  {
    title: 'Kho st nh hng',
    image: images.serviceIcon1,
    content: 'Thc hin bi kho st v nhn ngay kt qu v lnh vc ngnh ngh ph hp vi bn thn.'
  },
  {
    title: 'Tm hiu trng i hc',
    image: images.serviceIcon2,
    content: 'Tm thng tin chnh thng v cc trng i hc, tm ra ngi trng yu thch.'
  },
];

function Services() {

  const user = cookies.load('user');

  return (
    <section id="about-us" className="services">
      <div className="container-fluid">
        <div className="side-img">
          <img src={images.aside} alt="" />
        </div>
        <div className="side2-img">
          <img src={images.aside2} alt="" />
        </div>
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-12 col-lg-4 service-txt">
              <h2 className="service-title">Mi th bn cn, chng ti cung cp</h2>
              <div className="hero-btns service-btn">
                {user !== undefined ? (
                  <Link to="/main" data-scroll>
                    Tri nghim ngay
                  </Link>
                ) : (
                  <Link to="/login" data-scroll>
                    ng nhp
                  </Link>
                )}
              </div>
            </div>
            
            {services.map((item, index) => (
              <div key={index} className="col-12 col-sm-6 col-lg-4">
                <div className="item">
                  <img src={item.image} alt=""/>
                  <h3>{item.title}</h3>
                  <p>{item.content}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}

export default Services;
