import React, { useState } from "react";
import { HiMenuAlt4, HiX } from 'react-icons/hi';
import { motion } from 'framer-motion';
import { Link } from "react-router-dom";
import cookies from 'react-cookies';

import { images } from "../../constants";
import "../../css/bootstrap.min.css";
import "./Header.scss";

const navList = [
  {
    title: 'Trang ch',
    href: '#home'
  },
  {
    title: 'V chng ti',
    href: '#about-us'
  },
  {
    title: 'i hc',
    href: '#university'
  },
  {
    title: 'Phn hi',
    href: '#feedback'
  },
];

function Header() {

  const user = cookies.load('user');

  const [toggle, setToggle] = useState(false);

  return (
    <header id="home">
      <div className="container">
        <nav className="navbar navbar-expand-lg navbar-light ">
          {/* <!-- Change Logo Img Here --> */}
          <a className="navbar-brand" href="#">
            <img src={images.logo} alt="logo" />
          </a>

          <div className="app__navbar-menu">
            <HiMenuAlt4 onClick={() => setToggle(true)} />
            {toggle && (
              <motion.div
                whileInView={{ x: [300, 0] }}
                transition={{ duration: 0.85, ease: 'easeOut' }}
              >
                <HiX onClick={() => setToggle(false)} />
                <ul className="app__navbar-links">
                  {navList.map((item, index) => {
                    if (index === 0) 
                      return (<li key={index}>
                          <a 
                            data-scroll 
                            href={item.href}
                            onClick={() => setToggle(false)}
                          >
                            {item.title}
                          </a>
                        </li>);
                    else 
                      return (<li key={index}>
                          <a 
                            data-scroll 
                            href={item.href}
                            onClick={() => setToggle(false)}
                          >
                            {item.title}
                          </a>
                        </li>)
                  })}
                </ul>
              </motion.div>
            )}
          </div>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            {/* <!-- Nav Link --> */}
            <ul className="navbar-nav mr-auto">
              {navList.map((item, index) => {
                if (index === 0) 
                  return (<li key={index} className="nav-item active">
                    <a className="nav-link" data-scroll href={item.href}>{item.title}</a>
                  </li>);
                else 
                  return (<li key={index} className="nav-item">
                    <a className="nav-link" data-scroll href={item.href}>{item.title}</a>
                  </li>)
              })}
            </ul>
            <form
              data-scroll
              href="#feedback"
              className="contact-btn form-inline my-2 my-lg-0"
            >
              {/* <!-- Sign in Button --> */}
              {user !== undefined ? (
                <Link className="btn-signin" to="/main">Tri nghim ngay</Link>
              ) : (
                <Link className="btn-signin" to="/login">ng nhp</Link>
              )}
            </form>
          </div>
        </nav>
      </div>
      {/* <!-- HERO SECTION -->     */}
      <div className="container-fluid hero">
        <img src={images.hero} alt="hero-img" />
        <div className="container">
          {/* <!-- Hero Title --> */}
          <h1 className="hero-title">
            H thng h tr nh hng
            <br />
            ngh nghip cho hc sinh
          </h1>
          {/* <!-- Hero Title Info --> */}
          <p className="hero-content">
            Chng ti gip bn tm ra lnh vc m bn am m, t  gip xc nh
            ngnh ngh ph hp vi bn trong tng lai. ng thi bn c th tm kim
            ngi trng i hc m c ti y.
          </p>
          <div className="hero-btns">
            {user !== undefined ? (
              <Link className="active" to="/main" data-scroll href="#feedback">
                Tri nghim ngay
              </Link>
            ) : (
              <>
              <Link to="/register" data-scroll>
              ng k
            </Link>
              <Link to="/login" data-scroll href="#feedback">
                ng nhp
              </Link>
              </>
            )}
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
